      responseFormatType: 'text',
      stream: false,
      toolChoice: 'auto',
      user: '',
      selectedMcpAdapters: []
    };
    showAdvanced.value = false;
       showAdvanced.value = false;
  }
  showModal.value = true;
};

const closeModal = () => {
  showModal.value = false;
  isEditing.value = false;
